body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-41xl: 60px;
  --font-size-xl: 20px;

  /* Colors */
  --color-gray-100: #fffefe;
  --color-gray-200: #0a1723;
  --color-snow-100: #fffcfc;
  --color-snow-200: #fffbfb;
  --color-white: #fff;
  --color-black: #000;
  --color-gainsboro: #d9d9d9;
  --color-blueviolet-100: #4a46f8;
  --color-mediumblue: #3803cf;

  /* border radiuses */
  --br-8xs: 5px;
  --br-3xs: 10px;
}
